
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import AttendanceList from '@/components/attendance/AttendanceList'
import LingLoading from '@/components/attendance/LingLoading'
export default {
name: 'StudentList',
components: {
XHeader,
Group,
Cell,
AttendanceList,
Flexbox,
FlexboxItem,
LingLoading,
Divider
},
data() {
return {
list: [
{value: 'xxx', key: 0},
{value: 'xxx', key: 1},
{value: 'xxx', key: 2}
],
showContent: true,
titleArr: [
"", "", "", "", ""
],
attendanceList: [
{'name': 'name', 'arrive': '08:00', 'leave': '16:00', 'parent': ''}
]
}
},
computed: {
/**
* 
* @return {String} 
*/
getTitle: function() {
return this.$store.getters.getStudentAttendance.title
},
/**
* 
* @return {Array} 
*/
getAttendanceList: function() {
let arr=[], list = this.$store.getters.getStudentAttendance.studentDate
for(let l in list) {
arr.push({
name: list[l].name,
arrive: list[l].arrive,
leave: list[l].leave,
parent: list[l].parent?list[l].parent:'****'
})
}
return arr

},
/**
* 
* @returns {getLingLoading}
*/
getLoadingState: function() {
return this.$store.getters.getLingLoading
},
/**
* 
* @returns {*}
*/
getSelectTime: function() {
return this.$parent.$data.selectTime
}
},
methods: {
handleDetail: function(attendance) {
return

},
fetchData: function(formData = {}) {

let user = sessionStorage.getItem('CURRENT_USER')
this.$store.dispatch('actionStudentAttendance', {...{date: this.getSelectTime, classId: JSON.parse(user).classId}, ...formData})
}
},
watch: {
/**
* 
* @param val
*/
getSelectTime: function(val) {
this.fetchData({date: val})
}
},
created()  {
this.fetchData()
}
}



